<template>
  <div id="editor-md" class="main-editor">
    <textarea></textarea>
  </div>
</template>

<script>
  import $script from 'scriptjs';

  //
  import EMAttachment from "./EMAttachment.js"
  //
  import EMPicture from "./EMPicture.js"

  export default {
    data() {
      return {
        instance: null,
        emAttachment: new EMAttachment(),
        emPicture: new EMPicture(),
        innerValue: null
      }
    },
    props: {
      editorPath: {
        type: String,
        default: '/bystatic/fork/editormd'
      },
      jqueryPath: {
        type: String,
        default: '/bystatic/fork/jquery'
      },
      value: {
        type: String,
        required: false
      }
    },
    watch: {
      "value"(newVal, oldVal) {
        let that = this

        if (that.innerValue === newVal) {
          //
          console.log("")


        } else {

          //
          that.innerValue = newVal

          if (that.instance) {
            that.instance.setMarkdown(that.innerValue)
          }
        }


      }
    },
    created() {

    },
    methods: {
      initEditor() {
        let that = this
        let emAttachment = this.emAttachment
        let emPicture = this.emPicture
        let createElement = this.$createElement


        // Vue  DOM template  script 
        //  nextTick  editormd
        this.instance = window.editormd("editor-md", {
          width: '100%',
          height: 530,
          path: '/bystatic/fork/editormd/lib/', // Autoload modules mode, codemirror, marked... dependents libs path
          toolbarIcons: function () {
            // Or return editormd.toolbarModes[name]; // full, simple, mini
            // Using "||" set icons align right.
            return [
              "bold", "del", "italic", "quote", "ucwords", "uppercase", "lowercase", "|",
              "h1", "h2", "h3", "h4", "h5", "h6", "|",
              "list-ul", "list-ol", "hr", "|",
              "link", "reference-link", emPicture.name, emAttachment.name, "code", "preformatted-text", "code-block", "table", "datetime", "pagebreak", "|", "watch", "preview", "clear", "|", "undo", "redo"
            ]
          },
          //
          toolbarIconsClass: {
            attachment: emAttachment.icon,  // FontAawsome
            picture: emPicture.icon  // FontAawsome
          },
          // 
          toolbarHandlers: {
            /**
             * @param {Object}      cm         CodeMirror
             * @param {Object}      icon       jQuery
             * @param {Object}      cursor     CodeMirror
             * @param {String}      selection  
             */
            attachment: emAttachment.handler(createElement),
            picture: emPicture.handler(createElement)
          },
          //hover
          lang: {
            toolbar: {
              attachment: emAttachment.title,
              picture: emPicture.title
            }
          },

          markdown: that.innerValue,
          codeFold: true,
          saveHTMLToTextarea: true,
          searchReplace: true,
          htmlDecode: 'style,script,iframe|on*',
          emoji: false,
          taskList: true,
          tocm: true,                  // Using [TOCM]
          tex: true,                   // TeX
          flowChart: true,             // 
          sequenceDiagram: true,       // /,
          dialogLockScreen: true,   // true
          dialogShowMask: true,     // true
          dialogDraggable: false,    // true
          dialogMaskOpacity: 0.4,    // 0.1
          dialogMaskBgColor: "#000", // #fff
          onload: function () {

            if (that.innerValue) {
              that.instance.setMarkdown(that.innerValue)
            }

          },
          onchange: function () {

            that.innerValue = this.getMarkdown();

            // 
            that.$emit('input', that.innerValue);
            that.$emit('htmlChange', this.getHTML());

          }
        });

      }
    },
    mounted() {
      let that = this
      // async loading js dependencies
      // editormd depdend on jquery and zepto
      $script([
        `${that.jqueryPath}/dist/jquery.min.js`
      ], () => {
        $script(`${that.editorPath}/editormd.min.js`, () => {

          //nextTick
          setTimeout(function () {
            that.initEditor();
          }, 300)

        });
      });

      //

    },
    beforeDestroy() {
    }
  };
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../../../bystatic/fork/editormd/css/editormd.css";

  .main-editor {
    width: 100%;
    height: 100%;
  }

</style>



