import BaseEntity from '../base/BaseEntity'
import Filter from '../base/Filter'
import { simpleDateTime } from '../../filter/time'
import { Notification } from 'element-ui'
import Tank from '../tank/Tank'
import User from '../user/User'
import Tag from '../tag/Tag'

export default class Article extends BaseEntity {

  constructor (args) {
    super(args)

    //
    this.userUuid = null

    //
    this.title = null

    //
    this.tags = null

    //
    this.posterTankUuid = null

    //Url
    this.posterUrl = null

    //
    this.digest = null

    //markdown
    this.isMarkdown = true

    //markdown
    this.markdown = null

    //html
    this.html = null

    //
    this.privacy = false

    //
    this.top = false

    //
    this.agree = 0

    //
    this.words = 0

    //
    this.hit = 1

    //
    this.commentNum = 0

    //
    this.needNotify = true

    //
    this.posterTank = new Tank('image', false, 1024 * 1024, '1M')

    //
    this.user = new User()

    //
    this.agreed = false

    //
    this.tagArray = []

    //
    this.validatorSchema = {
      title: {
        rules: [{required: true, message: ''}, {max: 255, message: '255'}],
        error: null
      },
      tags: {
        rules: [{required: true, message: ''}, {max: 10000, message: '1024'}],
        error: null
      },
      html: {
        rules: [{required: true, message: ''}, {max: 10000, message: '10000'}],
        error: null
      },
      words: {
        rules: [{required: true, message: ''}],
        error: null
      }
    }

  }

  getFilters () {
    return [
      new Filter('SORT', '', 'orderSort'),
      new Filter('SORT', '', 'orderTop'),
      new Filter('SORT', '', 'orderHit'),
      new Filter('SORT', '', 'orderPrivacy'),
      new Filter('SORT', '', 'orderReleaseTime'),
      new Filter('INPUT', '', 'userUuid'),
      new Filter('CHECK', '', 'privacy'),
      new Filter('INPUT', '', 'title'),
      new Filter('INPUT', '', 'tag'),
      new Filter('INPUT', '', 'keyword')
    ]
  };

  render (obj) {
    super.render(obj)
    this.renderEntity('posterTank', Tank)
    this.renderEntity('user',User)
    this.renderList('tagArray',Tag)
  }

  getForm () {
    return {
      title: this.title,
      tags: this.tags,
      posterTankUuid: this.posterTankUuid,
      posterUrl: this.posterUrl,
      digest: this.digest,
      isMarkdown: this.isMarkdown,
      markdown: this.markdown,
      html: this.html,
      words: this.words,
      privacy: this.privacy,
      top: this.top,
      needNotify: this.needNotify,
      uuid: this.uuid ? this.uuid : null
    }
  }

  validate () {
    if (this.posterTank) {
      this.posterTankUuid = this.posterTank.uuid
      this.posterUrl = this.posterTank.url
    }

    if (!this.html || this.html.length > 10000) {
      Notification.error({
        message: '10000'
      })
    }

    return super.validate()
  }

}



